<template>
  <v-sheet>
    <v-container class="my-5">
      <v-card-title>
        - VELOSITE :
      </v-card-title>
      <v-row>
        <v-col
        cols="12"
        xl="3"
        lg="6"
        md="6"
        sm="6"
        xs="12"
        >
          <v-card  >
            <v-card-title>
               
            </v-card-title>
            <v-sheet min-height="140">
              <v-card-text>
                 900     40 .     ,  ,   
              </v-card-text>
            </v-sheet>
            <v-row
            justify="end"
            class="ma-0 pa-0">
              <v-btn
              color="primary"
              class="mb-4
              mr-2">
                <router-link
                color="white"
                to="/catalog"
                tag="span"
                style="cursor:pointer" >
                    
                </router-link>
              </v-btn>
            </v-row>
          </v-card>
        </v-col>
        <v-col
        cols="12"
        xl="3"
        lg="6"
        md="6"
        sm="6"
        xs="12"
        >
          <v-card>
            <v-card-title>
               
            </v-card-title>
            <v-sheet min-height="140">
              <v-card-text>
                    3 000       !
              </v-card-text>
            </v-sheet>
            <v-row
            justify="end"
            class="ma-0 pa-0">
              <v-btn
              color="primary"
              class="mb-4 mr-2">
                <router-link
                color="white"
                to="/delivery"
                tag="span"
                style="cursor:pointer" >
                    
                </router-link>
              </v-btn>
            </v-row>
          </v-card>
        </v-col>
        <v-col
        cols="12"
        xl="3"
        lg="6"
        md="6"
        sm="6"
        xs="12"
        >
          <v-card>
            <v-card-title>
                
            </v-card-title>
            <v-sheet min-height="140">
              <v-card-text>
                       ,         
              </v-card-text>
            </v-sheet>
            <v-row
            justify="end"
            class="ma-0 pa-0">
              <v-btn
              color="primary"
              class="mb-4 mr-2">
                <router-link
                color="white"
                to="/catalog?discount=true"
                tag="span"
                style="cursor:pointer" >
                    
                </router-link>
              </v-btn>
            </v-row>
          </v-card>
        </v-col>
        <v-col
        cols="12"
        xl="3"
        lg="6"
        md="6"
        sm="6"
        xs="12"
        >
          <v-card>
            <v-card-title>
                
            </v-card-title>
            <v-sheet min-height="140">
              <v-card-text>
                     ?   !    
              </v-card-text>
            </v-sheet>
            <v-row
            justify="end"
            class="ma-0 pa-0"
            >
              <v-btn
              color="primary"
              class="mb-4 mr-2"
              v-on:click="consultation = !consultation">
                 
              </v-btn>
            </v-row>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-dialog
    v-model="consultation"
    width="500"
    >
      <v-card>
        <v-card-title class="headline primary white--text">
           
        </v-card-title>
        <v-card-text class="py-6">
               
        </v-card-text>
        <v-form
        v-model="valid"
        ref="form"
        lazy-validation>
          <v-text-field
          class="px-5 py-3"
          v-on:input="sendForm=false"
          v-model="name"
          :rules="nameRules"
          label=" "
          required>
          </v-text-field>
          <v-text-field
          class="px-5 py-3"
          v-on:input="sendForm=false"
          v-model="phone"
          v-on:click="phone == '' ? phone = '+7' : phone == phone"
          :rules="phoneRules"
          label=" "
          required
          >
          </v-text-field>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
            v-if="sendForm==false"
            class="mt-10 pa-5"
            color="primary"
            large
            v-on:click="validate()"
            >
               
            </v-btn>
            <v-btn
            v-else
            class="mt-10 pa-5"
            color="success"
            large ><v-icon
            >mdi-check</v-icon>
              
            </v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
  </v-sheet>
</template>
<script>
export default {
  data(){
    return{
      consultation:false,
      valid:false,
      sendForm:false,
      name:'',
      phone:'',
      phoneRules: [
        v => !!v || ' ""    ',
        v => /^(\+7)/.test(v) || ' ""    "+7"',
        v => /^(\+7)([\D]*[0-9]{1}[\D]*){10}$/.test(v) || ' ""   11 ',
        v => /^(\+7)([^A-Fa-f]*[0-9]{1}[^A-Fa-f]*)$/.test(v) || ' ""    ',
      ],
      nameRules: [
        v => !!v || ' ""    ',
        v => /^[a-z, -, A-Z, -]+$/.test(v) || ' ""    ',
      ],
    }
  },
  methods:{
    validate () {
      if(this.$refs.form.validate()){
        this.sendForm=true
      }
    },
  },
}
</script>
