<template >
    <v-sheet>
      <v-row class="mx-6">
        <v-col cols="12"
        xl="6"
        lg="6"
        md="12"
        sm="12"
        xs="12"
        v-if="USER_CART.length>0">
          <v-card class="ma-3" >
            <v-simple-table>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">
                       
                    </th>
                    <th class="text-left">
                      
                    </th>
                    <th class="text-left">
                      
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                  v-for="product of productInCart"
                  :key="product._id">
                    <td>
                      <router-link
                      class="text-decoration-none"
                      :to="'/catalog/product?id='+product._id"
                      color="white--text"
                      tag="a"
                      style="cursor:pointer"
                      >{{product.model}}</router-link>
                    </td>
                    <td>
                      <v-btn icon v-on:click="changeCountProducts(-1, product._id)"><v-icon>mdi-minus</v-icon></v-btn>
                      {{USER_CART.filter(i => i.id == product._id)[0].count}}
                      <v-btn icon v-on:click="changeCountProducts(1, product._id)"><v-icon>mdi-plus</v-icon></v-btn>
                    </td>
                    <td>{{product.price | correctPrice}}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
            <v-row>
              <v-col>
                <v-card>
                  <h1  class="float-right pa-5"> {{totalPrice | correctPrice}}</h1>
                </v-card>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
        <v-col cols="12"
        xl="6"
        lg="6"
        md="12"
        sm="12"
        xs="12"
        v-if="USER_CART.length>0"
        >
          <v-card class="ma-3">
            <v-form v-model="valid" ref="form" lazy-validation>
              <h1 class='pa-5'> </h1>
              <v-row cols="12"
              xl="3"
              lg="3"
              md="6"
              sm="6"
              xs="6"
              >
                <v-col class="px-10">
                  <v-row>
                    <v-col>
                      <v-card-title > </v-card-title>
                    </v-col>
                    <v-col>
                      <v-text-field
                      v-if="phone !=undefined"
                      v-model="phone"
                      :rules="phoneRules"
                      v-on:click="phone == '' ? phone = '+7' : phone == phone"
                      label="+7 900 000 00 00"
                      required
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col>
                      <v-card-title height="60"> </v-card-title>
                    </v-col>
                    <v-col>
                      <v-text-field
                      v-model="name"
                      :rules="nameRules"
                      label=""
                      required
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col>
                      <v-card-title height="60"> </v-card-title>
                    </v-col>
                    <v-col>
                      <v-text-field
                      v-model="email"
                      :rules="emailRules"
                      label="E-mail"
                      required
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col>
                      <v-card-title height="60"> </v-card-title>
                    </v-col>
                    <v-col>
                      <v-text-field
                      v-model="city"
                      :rules="cityRules"
                      label=". "
                      required
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row >
                    <v-col>
                      <v-card-subtitle>  " ",    " "</v-card-subtitle>
                    </v-col>
                    <v-col class="justify-end d-flex" >
                      <v-hover v-slot:default="{ hover }" >
                        <v-btn
                        :elevation="hover ? 6 : 2"
                        v-on:click="validate()"
                        x-large
                        color="primary white--text"
                        >
                         
                      </v-btn>
                    </v-hover>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-form>
        </v-card>
      </v-col>
      <v-col  v-else>
        <v-card>
          <v-alert
          class="mt-2"
          border="left"
          color="deep-orange darken-1 white--text"
          >
           !
          <router-link
          class="text-decoration-underline"
          to="/catalog/"
          color="white--text"
          tag="span"
          style="cursor:pointer"
          >  </router-link>
        </v-alert>
      </v-card>
    </v-col>
  </v-row>
  <v-dialog
  v-model="dialog"
  width="500"
  >
  <v-card>
    <v-card-title class="headline primary white--text">
        !
    </v-card-title>
    <v-card-text class="py-6">
            10-15     
    </v-card-text>
    <v-divider></v-divider>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
      color="primary"
      text
      @click="dialog = false"
      >
      
    </v-btn>
  </v-card-actions>
</v-card>
</v-dialog>
</v-sheet>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  data(){
    return{
      dialog: false,
      valid: false,
      phone: '',
      phoneRules: [
        v => !!v || ' ""    ',
        v => /^(\+7)/.test(v) || ' ""    "+7"',
        v => /^(\+7)([\D]*[0-9]{1}[\D]*){10}$/.test(v) || ' ""   11 ',
        v => /^(\+7)([^A-Fa-f]*[0-9]{1}[^A-Fa-f]*)$/.test(v) || ' ""    ',
      ],
      name: '',
      nameRules: [
        v => !!v || ' ""    ',
        v => /^[a-z, -, A-Z, -]+$/.test(v) || ' ""    ',
      ],
      email: '',
      emailRules: [
        v => !!v || ' E-mail    ',
        v => /.+@.+/.test(v) || ' "E-mail"   ',
      ],
      city: '',
      cityRules: [
        v => !!v || ' " "    ',
      ],
    }
  },
  methods:{
    ...mapActions(['FETCH_PRODUCTS', 'CREATE_CART']),
    getId(){
    },
    //   .
    changeCountProducts(count, productId){
      let userCart = []
      userCart = this.USER_CART
      console.log(userCart)
      let index = userCart.findIndex(elem => elem.id == productId)
      userCart[index].count = userCart[index].count + count
      if(userCart[index].count <= 0){
        userCart.splice(index, 1)
      }
      console.log(userCart[index])
      this.CREATE_CART(userCart)
      this.USER_CART
    },
    //     " "
    validate () {
      if(this.$refs.form.validate()){
        this.dialog = true
      }
    },
  },
  filters:{
    correctPrice: function (num) {
      if(num){
        return Number(num).toLocaleString('ru', {style: 'currency', currency: 'RUB', minimumFractionDigits: '0'})
      }
    },
  },
  computed:{
    ...mapGetters(['ALL_PRODUCTS', 'USER_CART']),
    //   
    productInCart(){
      let cartProduct = []
      for(let productId of this.USER_CART){
        let filterProduct
        filterProduct = this.ALL_PRODUCTS.filter(product => product._id == productId.id)[0]
        cartProduct.push(filterProduct)
      }
      return cartProduct
    },
    // 
    totalPrice(){
      let total = 0
      this.productInCart.map(product => total = total + Number(product.price * this.USER_CART.filter(elem => elem.id == product._id)[0].count))
      return total
    }
  },
  async mounted(){
    this.FETCH_PRODUCTS()
  },
}
</script>
